import './InnerLink.scss';

import React from 'react';
import { LinkExternal, } from './Link';



export default function InnerLinkExternal({ children, href, text, }) {
  if (text)
    return (
      <LinkExternal href={href} className="inner__link">{text}</LinkExternal>
    );

  return (
    <LinkExternal href={href} className="inner__link">{children}</LinkExternal>
  );
};
